/*
*
* =====================
* GENERAL
* =====================
*
*/

.text-shadow {
    text-shadow: 0 1px 2px rgba(0,0,0,0.6);
}

.text-small {
    font-size: 0.9rem !important;
}

.page-holder {
    background-size: cover !important;
    background-position: center center !important;
    min-height: 100vh;

    .overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.3);
    }
}

.video-bg {
    width: 100%;
    height: 100%;
}


.social-link {
    width: 26px;
    height: 26px;
    border-radius: 50%;
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $brand-primary;

    i {
        font-size: 0.9rem;
    }

    &:hover, &:focus {
        text-decoration: none;
        color: #fff;
    }

    &.facebook {
        background: #4460ae;
    }
    &.twitter {
        background: #3cf;
    }
    &.google-plus {
        background: #c21f25;
    }
    &.instagram {
        background: #cd4378;
    }
}


.font-family-base {
    font-family: $font-family-base !important;
}

.form-control.transparent-bg {
    background: rgba(255, 255, 255, .3) !important;
    color: $white !important;
    &::placeholder {
        color: $white;
        opacity: 1;
      }
}
